import React, { forwardRef, useRef } from "react";
import { getUserName, getUserRol } from "../../functions/user";
import { MenuList } from "./MenuList";

export const NavBar = forwardRef((props, ref)=>{
    const menu = [
        {
            text: "HOME",
            link: "/home",
            icon: "dashboard"
        },
        {
            text: "ADMINISTRACION",
            link: "#!",
            icon: "admin_panel_settings",
            submenus: [
                {
                    text: "USUARIOS",
                    link: "/admin",
                    icon: "manage_accounts"
                },
                // {
                //     text: "COMPANY",
                //     link: "#!",
                //     icon: "zmdi-balance",
                // },
                {
                    text: "PROVEEDORES",
                    link: "/notDo",
                    icon: "local_shipping"
                },
                {
                    text: "FORMAS DE PAGO",
                    link: "/notDo",
                    icon: "payments"
                },
                {
                    text: "CATEGORÍAS",
                    link: "/categories",
                    icon: "sell"
                },
                {
                    text: "PRODUCTOS",
                    link: "/notDo",
                    icon: "inventory"
                },
                {
                    text: "INVENTORY",
                    link: "/inventory",
                    icon: "inventory"
                },
                {
                    text: "MESAS",
                    link: "/mesa",
                    icon: "event_seat"
                },
                {
                    text: "CAJAS",
                    link: "/caja",
                    icon: "attach_money"
                }
            ]
        },
        {
            text: "PEDIDOS (mesero)",
            link: "/notDo",
            icon: "sports_bar"
        },
        {
            text: "CAJA (cajero)",
            link: "/notDo",
            icon: "shopping_basket"
        },
        {
            text: "SALES (informe)",
            link: "/notDo",
            icon: "real_estate_agent"
        }
    ];

    const handleCloseClick = ()=>{
        props.toggleMenu();
    }


    return (
        <header ref={ref} className="open">
            <div className="page-tittle tittles">
                <span>Inventario</span>
                <span onClick={handleCloseClick} className="material-icons header-close-button">cancel</span>
            </div>
            <div className="user-content">
                <img src="assets/img/avatar-male.png" alt="Avatar" />
                <figcaption>
                    <span id="nombreusuario" >
                        {getUserName()}
                        <br/>
                        <small id="rolusuario">{getUserRol()}</small>
                    </span>
                </figcaption>
            </div>
            <nav>
                <MenuList menu={menu} />
            </nav>
        </header>
    )

});