import React, {useRef,forwardRef,useImperativeHandle } from "react";
import { MenuListItem } from "./MenuListItem";


export const SubMenuList = forwardRef((props, ref) => {
    const menu = useRef()

    useImperativeHandle(ref, () => ({
        toogleMenu(){
            menu.current.classList.toggle("open");
        },
    }))

    if(props.subMenu){
        return (
            <ul className="open" ref={menu}>
                {props.subMenu.map((subMenuItem,index) =>(<MenuListItem key={index} menuItem={subMenuItem}/>))}
            </ul>
        )
    }
});