import React, {useRef} from "react";
import {SubMenuList} from "./MenuSubList";
import { Link } from "react-router-dom";

export function MenuListItem({menuItem}){
    const {text, icon, link, submenus} = menuItem

    const subMenuLista = useRef();

    if(submenus){
        var arrowIcon = <span className="material-icons dropdown">arrow_drop_down</span>
        var subMenu = <SubMenuList ref={subMenuLista} subMenu={submenus}/>
    }

    return (
        <li>
            <a href={link} onClick={() => subMenuLista.current.toogleMenu()}>
                <span className="material-icons">{icon}</span>
                <span>{text}</span>
                {arrowIcon}
            </a>
            {subMenu}
        </li>
    )
}