import React, {useState} from "react";
import { Container } from "@mui/material";
import { Tab, Tabs } from "@mui/material";

export function TabPane({props}){
    const {titulos, create, list} = props

    const [value, setValue] = useState(0);

    const handleTabs = (e,val)=>{
        setValue(val);
    };

    return (
        <Container>
            <Tabs value={value} onChange={handleTabs} textColor="secondary" indicatorColor="secondary" centered>
                <Tab label={titulos[0]}/>
                <Tab label={titulos[1]}/>
            </Tabs>
            <TabPanel value={value} index={0} content={create}></TabPanel>
            <TabPanel value={value} index={1} content={list}></TabPanel>
        </Container>
    )
}

function TabPanel(props){
    const {children, value, index, content} = props

    if(value == index){
        return (
            <div style={{marginTop: "40px"}}>
                {content}
            </div>
        );
    }
}