import React, { useState, useEffect, useRef } from "react";
import { ListPanel } from "../components/general/Lista/ListPanel";
import { TabPane } from "../components/general/TabPane/TabPane";
import {Form} from "../components/general/FormPanel/FormPanel";
import { MenuItem, Select, Stack, TextField } from "@mui/material";
import {getUsuarios, disableUser, getTipoIdentificacion, getRol, createUsuario} from "../functions/api";

export function Admin(){
     //estado mesas
    const [usuarios, setUsuarios] = useState([]);
     //estado de valores para el formulario
    const [estadoFormulario, setEstadoFormulario] = useState({
        identificacion: {
            error: false,
            helpText: "",
        },
        nombre:{
            error: false,
            helpText: "",
        },
        celular:{
            error: false,
            helpText: "",
        },
        telefono:{
            error: false,
            helpText: "",
        },
        direccion:{
            error: false,
            helpText: "",
        },
        usuario:{
            error: false,
            helpText: "",
        },
        password:{
            error: false,
            helpText: "",
        },
        tipoIdentificacion: {
            value: 1,
            options: [{id: 1, name: "Tipo identificación",}]
        },
        rol:{
            value: 1,
            options: [{id: 1, name: "Roles",}]
        }
    })

    var updateUsuariosEstado = ()=>{
        getUsuarios().then(data =>{
            var usuarios = data.map((user)=>{
                user.secondaryText = user.rol
                return user;
            });
            usuarios.sort((a,b) => a.name < b.name ? -1: 1)
            setUsuarios(usuarios);
        })
    }
    //obtener las categorias al cargar la pagina
    useEffect(()=>{
        //consulta la lista de usuarios en la api y actualiza
        updateUsuariosEstado();

        //consulta los roles en la api
        getTipoIdentificacion().then(data =>{
            var tipos = data.map((tipo)=>{
                return {id: tipo.id, name: tipo.name};
            })

            var estado = {...estadoFormulario}
            estado.tipoIdentificacion.options = tipos
            setEstadoFormulario(estado);
        });

        getRol().then(data =>{
            var roles = data.map((rol)=>{
                return {id: rol.id, name: rol.name};
            })

            var estado = {...estadoFormulario}
            estado.rol.options = roles
            setEstadoFormulario(estado);
        });

    },[]);
    
    //#region lista
    //funcion para el filtro
    const handleSearchKeyUp = (ref)=>{
        const text = ref.current.value;
        if(text.length == 0 ){
            var allItems = [...usuarios];
            allItems.map(cat =>{
                cat.visible = true;
            })
            setUsuarios(allItems);
            return
        }

        var filter = usuarios.map(item => {
            let shouldRender = item.name.toLowerCase().indexOf(text.toLowerCase()) >= 0
            item.visible = shouldRender
            return item;
        });
        setUsuarios(filter);
    }
    //envia los datos al panel inferior
    const listPanelData = {
        title: "Lista de usuarios",
        searchActive: true,
        searchFunction: handleSearchKeyUp,
    }
    //#endregion

    //#region desactivar
    const handleDisableUser = (id, activo)=>{
        disableUser(id, activo)
        updateUsuariosEstado();
    }
    listPanelData.disableFunction = handleDisableUser
    //#endregion

    //#region crear
    //inputs formulario
    const identificacionInput =  useRef();
    const nombreInput = useRef();
    const celularInput = useRef();
    const telefonoInput = useRef();
    const direccionInput = useRef();
    const usuarioInput = useRef();
    const contrasenaInput = useRef();

    //funcion de envio de datos
    const handleAddMesa = (e)=>{
        e.preventDefault();
        var estado = {...estadoFormulario}
        var errores = false

        var identificacion = identificacionInput.current.value
        var nombre = nombreInput.current.value
        var celular = celularInput.current.value
        var telefono = telefonoInput.current.value
        var direccion = direccionInput.current.value
        var usuario = usuarioInput.current.value
        var contrasena = contrasenaInput.current.value


        if(identificacion == ""){
            estado.identificacion.error = true
            estado.identificacion.helpText = "Este campo es obligatorio"
            errores = true
        }
        if(identificacion.length < 10 || identificacion.length > 20 ){
            estado.identificacion.error = true
            estado.identificacion.helpText = "La longitud debe estar entre 10 y 20"
            errores = true
        }

        if(nombre == ""){
            estado.nombre.error = true
            estado.nombre.helpText = "Este campo es obligatorio"
            errores = true
        }
        if(celular == ""){
            estado.celular.error = true
            estado.celular.helpText = "Este campo es obligatorio"
            errores = true
        }
        if(celular.length < 10){
            estado.celular.error = true
            estado.celular.helpText = "La longitud no debe ser menor a 10"
            errores = true
        }
        if(telefono == ""){
            estado.telefono.error = true
            estado.telefono.helpText = "Este campo es obligatorio"
            errores = true
        }
        if(telefono.length < 7){
            estado.telefono.error = true
            estado.telefono.helpText = "La longitud no debe ser menor a 10"
            errores = true
        }
        if(direccion == ""){
            estado.direccion.error = true
            estado.direccion.helpText = "Este campo es obligatorio"
            errores = true
        }
        if(usuario == ""){
            estado.usuario.error = true
            estado.usuario.helpText = "Este campo es obligatorio"
            errores = true
        }
        if(contrasena == ""){
            estado.password.error = true
            estado.password.helpText = "Este campo es obligatorio"
            errores = true
        }

        setEstadoFormulario(estado);

        if(errores) return;

        var usuario = {
            "tercero":{
                "identificacion": identificacion,
                "nombre": nombre,
                "tipoIdentificacion": estado.tipoIdentificacion.value,
                "celular": celular,
                "telefono": telefono,
                "direccion": direccion
            },
            "username": usuario,
            "password": contrasena,
            "idRol": estado.rol.value
        }

        createUsuario(usuario)
        setTimeout(updateUsuariosEstado, 1000);
    };


    var inputStyles ={
        margin: "10px !important",
        width: "45%"
    }
    var createContent = (
        <Form title={"Crear usuario"} submitHanddler={handleAddMesa}>
            <Stack direction="row" spacing={2} alignItems="baseline" sx={{flexWrap:"wrap"}}>
                <TextField
                    inputRef={identificacionInput}
                    label="Identificacion"
                    sx={inputStyles}
                    type="number"
                    error={estadoFormulario.identificacion.error}
                    helperText={estadoFormulario.identificacion.helpText}
                />
                <TextField
                    inputRef={nombreInput}
                    label="Nombre"
                    sx={inputStyles}
                    error={estadoFormulario.nombre.error}
                    helperText={estadoFormulario.nombre.helpText}
                />
                <Select
                    value={estadoFormulario.tipoIdentificacion.value}
                    label="Tipo Identificación"
                    sx={inputStyles}
                >
                        {estadoFormulario.tipoIdentificacion.options.map((option) => <MenuItem key={option.id} value={option.id}>{option.name}</MenuItem>)}
                </Select>

                <TextField
                    inputRef={celularInput}
                    label="Celular"
                    sx={inputStyles}
                    type="number"
                    error={estadoFormulario.celular.error}
                    helperText={estadoFormulario.celular.helpText}
                />
                <TextField
                    inputRef={telefonoInput}
                    label="Telefono"
                    sx={inputStyles}
                    type="number"
                    error={estadoFormulario.telefono.error}
                    helperText={estadoFormulario.telefono.helpText}
                />
                <TextField
                    inputRef={direccionInput}
                    label="Direccion"
                    sx={inputStyles}
                    error={estadoFormulario.direccion.error}
                    helperText={estadoFormulario.direccion.helpText}
                />

                <TextField
                    inputRef={usuarioInput}
                    label="Correo usuario"
                    sx={inputStyles}
                    error={estadoFormulario.usuario.error}
                    helperText={estadoFormulario.usuario.helpText}
                    type="email"
                />
                <TextField
                    inputRef={contrasenaInput}
                    label="Contraseña"
                    sx={inputStyles}
                    type="password"
                    error={estadoFormulario.password.error}
                    helperText={estadoFormulario.password.helpText}
                />
                <Select
                    value={estadoFormulario.rol.value}
                    label="Rol"
                        //* onChange={handleChange}
                    sx={inputStyles}
                >
                    {estadoFormulario.rol.options.map((option) => <MenuItem key={option.id} value={option.id}>{option.name}</MenuItem>)}
                </Select>
            </Stack>

            {/* <TextField inputRef={NombreInput} label="Numero mesa" variant="standard" fullWidth={true} InputProps={{
                startAdornment: (
                    <span className="material-icons">event_seat</span>
                ),
            }}/> */}
        </Form>
    )
    //#endregion



    //#region render
    const tabProps = {
        titulos: ["Crear Usuario", "Lista de usuarios"],
        create: createContent,
        list: <ListPanel panelData={listPanelData} listData={usuarios}/>,
    }
    return (
        <TabPane props={tabProps}></TabPane>
    )
    //#endregion
} 