import React, { Fragment } from "react";
import { IconButton, Paper, Stack } from "@mui/material";
import { Box } from "@mui/system";

export function Form(props){
    const {title, children, submitHanddler} = props

    const handleIconClick = submitHanddler;

    const paperStyles = {
        bgcolor: '#2e6da4',
        color: "#fff",
        fontSize: "18px",
        padding: 1,
        textAlign: "center",
        borderBottomLeftRadius: 0,
        borderBottomRightRadius: 0,
    }

    var boxStyles = {
        padding: "20px",
        boxShadow: "1px 1px 3px 0px #000",
        // borderTopLeftRadius: "10px",
        // borderTopRightRadius: "10px",
    }
    
    return (
        <Fragment>
            <Paper sx={paperStyles} variant="square">
                {title}
            </Paper>
            <Box sx={boxStyles}>
                {children}

                <div style={{textAlign: "center", marginTop: "50px"}}>
                    <IconButton onClick={handleIconClick}>
                        <span style={{fontSize: "50px", color: "#2e6da4"}} className="material-icons">add_circle</span>
                    </IconButton>
                </div>
            </Box>
        </Fragment>
    )
}