import { getServerUrl } from "./configuration";

//configuraciones del localeStorage
const KEYLOGIN = "login.token"
const SERVERURL = getServerUrl();

//respuesta de la api
var apiResponse = {
    data: null,
    mensaje: null,
    statusCode: null,
    statusCodeApi: null,
    error: null
}

function ResetResponse(){
    apiResponse.data = null;
    apiResponse.mensaje = null
    apiResponse.statusCode = null
}

function QueryData(method, url, callback, body = null, headers = null){
    ResetResponse();
    var localHeaders= {
        'Authorization': 'BEARER ' + localStorage.getItem(KEYLOGIN),
    };
    if(body){
        var postHeaders = {
            'Accept': 'application/json;charset=utf-8',
            'Content-Type': 'application/json;charset=utf-8',
        }
    }
    var fullHeaders = Object.assign({}, headers, localHeaders,postHeaders);

    return fetch(SERVERURL + url,{
        method: method,
        headers: fullHeaders,
        body: body,
    })//Metodos genericos en caso de que la api tenga StatusCode de errores
    .then(response =>{
        if(response.status == 500){
            apiResponse.error = "Eror interno del servidor"
        }
        if(response.status == 401){
            alert("inice sesión");
            window.localStorage.removeItem(KEYLOGIN);
            window.location.href = "/";
        }
        return response;
    }).then(response =>{

        apiResponse.statusCode = response.status;
        try{
            return response.json();
        }
        catch(error){
            apiResponse.error = error;
        }
    }).then(jsonData =>{
        apiResponse.data = jsonData.data;
        apiResponse.mensaje = jsonData.status.message;
        apiResponse.statusCodeApi = jsonData.status.code;
        
        return apiResponse;
    })
    .then(callback);
}


//#region categorias
export function getCategories(){
    var data = QueryData("get", "/api/categoria/", function(data){
        if(data.error != null){
            alert(data.error);
            return;
        }

        if(data.statusCode == 200){
            var categorias = data.data.map(cat => ({id: cat.Id, name: cat.Nombre, visible: true, icon: "label"}));
        }
    
        return categorias;
    });
    return data;
}

export function createCategory(nombre){
    if(nombre != null){
        QueryData("POST", "/api/categoria/", function(data){
            if(data.mensaje != null && data.mensaje != ""){
                alert(data.mensaje);
                return;
            }
            alert("Categoria creada");
        }, JSON.stringify({nombre: nombre}));
        return;
    }
    throw "El parametro nombre no debe ser nulo";
}

export function deleteCategory(id){
    if(id != null){
        QueryData("DELETE", "/api/categoria/", function(data){
            if(data.mensaje != null && data.mensaje != ""){
                alert(data.mensaje);
                return;
            }
        }, JSON.stringify({id: id}));
        return;
    }
    throw "El parametro id no debe ser nulo";
}

export function updateCategory(id, nombre){
    if(nombre != null){
        QueryData("PUT", "/api/categoria/", function(data){
            if(data.mensaje != null && data.mensaje != ""){
                alert(data.mensaje);
                return;
            }
            alert("Categoria creada");
        }, JSON.stringify({id: id, nombre: nombre}));
        return;
    }
    throw "El parametro nombre no debe ser nulo";
}

