import React, { Fragment } from "react";
import {
    BrowserRouter,
    Routes,
    Route,
    Link
} from "react-router-dom";
import {Home} from './pages/home'
import { Login } from "./pages/login";
import { Admin } from "./pages/admin";
import { Categories } from "./pages/categories";
import { Inventory } from "./pages/inventory";
import { InventoryDetail } from "./pages/inventoryDetail";
import { NotImplemented } from "./pages/NotImplemented";
import { Menu } from "./components/menu/menu";
import { ThemeProvider } from "@mui/material";
import {myTheme} from "./functions/myTheme";
import { Mesa } from "./pages/mesa";
import { Caja } from "./pages/caja";


export function App() {

    var isLogin = true;
    var theme = myTheme();

    if(isLogin){
        return (
            <ThemeProvider theme={theme}>
                <Menu>
                    <BrowserRouter>
                        <Routes>
                            <Route path="/" element={<Login/>}></Route>

                            {/* Administracion */}
                            <Route path="/mesa" element={<Mesa/>}></Route>
                            <Route path="/caja" element={<Caja/>}></Route>

                            <Route path="/home" element={<Home/>}></Route>
                            <Route path="/admin" element={<Admin/>}></Route>
                            <Route path="/categories" element={<Categories/>}></Route>
                            <Route path="/product" element={<Inventory/>}></Route>
                            <Route path="/product/:productId" element={<InventoryDetail/>}/>
                            <Route path="*" element={<NotImplemented/>}></Route>
                        </Routes>
                    </BrowserRouter>
                </Menu>
            </ThemeProvider>
        )
    }else{
        return ""
    }

    return; 
    // (
    //     // <BrowserRouter>
    //     //     <Routes>
    //     //         <Route path="/"></Route>
    //     //     </Routes>
    //     // </BrowserRouter>
    // )

    // return (
    //     <Menu>
    //         <BrowserRouter>
    //             <Routes>
    //                 <Route path="/" element={<Login/>}></Route>
    //                 <Route path="/home" element={<Home/>}></Route>
    //                 <Route path="/admin" element={<Admin/>}></Route>
    //                 <Route path="/categories" element={<Categories/>}></Route>
    //                 <Route path="/product" element={<Inventory/>}>
    //                 </Route>
    //                 <Route path="/product/:productId" element={<InventoryDetail/>}/>
    //                 <Route path="*" element={<NotImplemented/>}></Route>
    //             </Routes>
    //         </BrowserRouter>
    //     </Menu>
    // );
}