import React, {useRef, useState, useEffect, Fragment} from "react";
import { getServerUrl } from "../functions/configuration";
// import { CategoriesList } from "../components/categories/CategoriesList";
import { ListPanel } from "../components/general/Lista/ListPanel";
import { TabPane } from "../components/general/TabPane/TabPane";
import { Form } from "../components/general/FormPanel/FormPanel";
import { TextField } from "@mui/material";
import { getCategories, createCategory, deleteCategory, updateCategory } from "../functions/api";

const URL = getServerUrl() + "/api/categoria/";

export function Categories(){
    //crear componente con las categorias
    const [categories, setCategories] = useState([]);
    //hace un fetch al servidor y actualiza el estado
    var updateCategories = ()=>{
        getCategories().then(data =>{
            setCategories(data);
        })
    }
    //obtener las categorias al cargar la pagina
    useEffect(()=>{
        updateCategories();
    },[]);


    //#region lista categorias
    //funcion para el filtro de categorias
    const handleSearchKeyUp = (ref)=>{
        const text = ref.current.value;
        if(text.length == 0 ){
            var allCategories = [...categories];
            allCategories.map(cat =>{
                cat.visible = true;
            })
            setCategories(allCategories);
            return
        }

        var filter = categories.map(cat => {
            let shouldRender = cat.name.toLowerCase().indexOf(text.toLowerCase()) >= 0
            cat.visible = shouldRender
            return cat;
        });
        setCategories(filter);
    }
    //envia los datos al panel inferior
    const listPanelData = {
        title: "Lista de categorias",
        searchActive: true,
        searchFunction: handleSearchKeyUp,
    }
    //#endregion

    //#region crear categoria
    //agregar categoria
    const NombreInput =  useRef();
    const handleAddCategory = (e)=>{
        e.preventDefault();

        const nombre = NombreInput.current.value;

        if (nombre==null || nombre==""){
            alert("Ingrese una categoria") 
            return; 
        }

        createCategory(nombre);
        setTimeout(updateCategories, 1000);
    };
    var createContent = (
        <Form title={"Crear Categoría"} submitHanddler={handleAddCategory}>
            <TextField inputRef={NombreInput} label="Categoría" variant="standard" fullWidth={true} InputProps={{
                startAdornment: (
                    <span className="material-icons">local_offer</span>
                ),
            }}/>
        </Form>
    )
    //#endregion

    //#region editar categoria
    var editNombreInput = useRef();
    const handleEditCategory = (id, callbackClose)=>{
        var nombre = editNombreInput.current.value

        if(nombre == "" || nombre == null){
            alert("el nombre es obligatorio")
            return
        }

        updateCategory(id, nombre);
        updateCategories();
        callbackClose();
    }
    var editContent = (
        <TextField inputRef={editNombreInput} label="Categoría" variant="standard" fullWidth={true} InputProps={{
            startAdornment: (
                <span className="material-icons">local_offer</span>
            ),
        }}/>
    )
    listPanelData.editContent = editContent;
    listPanelData.editFunction = handleEditCategory;
    //#endregion

    //#region borrar categorias
    listPanelData.deleteFunction = (id)=>{
        deleteCategory(id)
        setTimeout(updateCategories, 1000);
    }
    //#endregion

    //#region render
    const tabProps = {
        titulos: ["Crear Categorías", "Lista Categorías"],
        create: createContent,
        list: <ListPanel panelData={listPanelData} listData={categories}/>,
    }
    return (
        <TabPane props={tabProps}></TabPane>
    )
    //#endregion
}