import React, {useState, useEffect, useRef} from "react";
import { ListPanel } from "../components/general/Lista/ListPanel";
import { TabPane } from "../components/general/TabPane/TabPane";
import { Form } from "../components/general/FormPanel/FormPanel";
import { TextField } from "@mui/material";
import { getCajas, createCaja, deleteCaja } from "../functions/api";

export function Caja(){
    //estado cajas
    const [cajas, setCajas] = useState([]);

    var updateCajasEstado = ()=>{
        getCajas().then(data =>{
            var cajas = data.map((caja)=>{
                if(caja.active){
                    caja.secondaryText = "Activo"
                }
                else{
                    caja.secondaryText = "Deshabilitado"
                }
                return caja;
            })
            setCajas(cajas);
        })
    }
    //obtener las categorias al cargar la pagina
    useEffect(()=>{
        updateCajasEstado();
    },[]);


    //#region lista
    // funcion para el filtro
    const handleSearchKeyUp = (ref)=>{
        const text = ref.current.value;
        if(text.length == 0 ){
            var allItems = [...cajas];
            allItems.map(cat =>{
                cat.visible = true;
            })
            setCajas(allItems);
            return
        }

        var filter = cajas.map(item => {
            let shouldRender = item.name.toLowerCase().indexOf(text.toLowerCase()) >= 0
            item.visible = shouldRender
            return item;
        });
        setCajas(filter);
    }
    //envia los datos al panel inferior
    const listPanelData = {
        title: "Lista de cajas",
        searchActive: true,
        searchFunction: handleSearchKeyUp,
    }
    //#endregion

    //#region crear
    const NombreInput =  useRef();
    const handleAddMesa = (e)=>{
        e.preventDefault();

        const nombre = NombreInput.current.value;

        if (nombre==null || nombre==""){
            alert("Ingrese una categoria") 
            return; 
        }

        createCaja(nombre);
        setTimeout(updateCajasEstado, 1000);
    };
    var createContent = (
        <Form title={"Crear caja"} submitHanddler={handleAddMesa}>
            <TextField inputRef={NombreInput} label="Numero caja" variant="standard" fullWidth={true} InputProps={{
                startAdornment: (
                    <span className="material-icons">attach_money</span>
                ),
            }}/>
        </Form>
    )
    //#endregion


    //#region borrar categorias
    listPanelData.deleteFunction = (id)=>{
        deleteCaja(id)
        setTimeout(updateCajasEstado, 1000);
    }
    //#endregion

    //#region render
    const tabProps = {
        titulos: ["Crear Caja", "Lista de Cajas"],
        create: createContent,
        list: <ListPanel panelData={listPanelData} listData={cajas}/>,
    }
    return (
        <TabPane props={tabProps}></TabPane>
    )
    //#endregion
}