import React, {useState, useEffect, useRef, Fragment} from "react";
import { getMesas, createMesa, deleteMesa, updateMesa } from "../functions/api";
import { ListPanel } from "../components/general/Lista/ListPanel";
import { TabPane } from "../components/general/TabPane/TabPane";
import { Form } from "../components/general/FormPanel/FormPanel";
import { TextField, Checkbox, FormControlLabel, FormControl } from "@mui/material";

export function Mesa(){
    //estado mesas
    const [mesas, setMesas] = useState([]);

    var updateMesasEstado = ()=>{
        getMesas().then(data =>{
            var mesas = data.map((mesa)=>{
                if(mesa.active){
                    mesa.secondaryText = "Activo"
                }
                else{
                    mesa.secondaryText = "Deshabilitado"
                }
                return mesa;
            })
            setMesas(mesas);
        })
    }
    //obtener las categorias al cargar la pagina
    useEffect(()=>{
        updateMesasEstado();
    },[]);

    //#region lista
    // funcion para el filtro
    const handleSearchKeyUp = (ref)=>{
        const text = ref.current.value;
        if(text.length == 0 ){
            var allMesas = [...mesas];
            allMesas.map(cat =>{
                cat.visible = true;
            })
            setMesas(allMesas);
            return
        }

        var filter = mesas.map(item => {
            let shouldRender = item.name.toLowerCase().indexOf(text.toLowerCase()) >= 0
            item.visible = shouldRender
            return item;
        });
        setMesas(filter);
    }
    //envia los datos al panel inferior
    const listPanelData = {
        title: "Lista de mesas",
        searchActive: true,
        searchFunction: handleSearchKeyUp,
    }
    //#endregion

    //#region crear
    const NombreInput =  useRef();
    const handleAddMesa = (e)=>{
        e.preventDefault();

        const nombre = NombreInput.current.value;

        if (nombre==null || nombre==""){
            alert("Ingrese una categoria") 
            return; 
        }

        createMesa(nombre);
        setTimeout(updateMesasEstado, 1000);
    };
    var createContent = (
        <Form title={"Crear mesa"} submitHanddler={handleAddMesa}>
            <TextField inputRef={NombreInput} label="Numero mesa" variant="standard" fullWidth={true} InputProps={{
                startAdornment: (
                    <span className="material-icons">event_seat</span>
                ),
            }}/>
        </Form>
    )
    //#endregion


    //#region editar
    var editNombreInput = useRef();
    // var ActiveCheckbox = useRef()
    const handleEditCategory = (id, callbackClose)=>{
        var nombre = editNombreInput.current.value
        // var active = ActiveCheckbox.current.checked

        if(nombre == "" || nombre == null){
            alert("el nombre es obligatorio")
            return
        }

        updateMesa(id, true, nombre);
        updateMesasEstado();
        callbackClose();
    }
    var editContent = (
        <FormControl>
            <TextField inputRef={editNombreInput} label="Numero mesa" variant="standard" fullWidth={true} InputProps={{
                startAdornment: (
                    <span className="material-icons">event_seat</span>
                ),
            }}/>
            {/* <FormControlLabel inputRef={ActiveCheckbox} defaultChecked control={<Checkbox/>} label="Activo" sx={{marginTop: "20px"}}></FormControlLabel> */}
        </FormControl>
    )
    listPanelData.editContent = editContent;
    listPanelData.editFunction = handleEditCategory;
    //#endregion

    //#region borrar categorias
    listPanelData.deleteFunction = (id)=>{
        deleteMesa(id)
        setTimeout(updateMesasEstado, 1000);
    }
    //#endregion


    //#region render
    const tabProps = {
        titulos: ["Crear Mesa", "Lista de Mesas"],
        create: createContent,
        list: <ListPanel panelData={listPanelData} listData={mesas}/>,
    }
    return (
        <TabPane props={tabProps}></TabPane>
    )
    //#endregion
}