import React, { Fragment, useRef, useState } from "react";
import { DialogContent, Paper, TextField } from "@mui/material";
import { Box } from "@mui/system";
import { List, ListItem, Avatar,ListItemAvatar, IconButton, ListItemText} from "@mui/material";
import { Dialog, DialogTitle, DialogActions, Button} from "@mui/material";
import { Switch } from "@mui/material";
import { FormControlLabel } from "@mui/material";

export function ListPanel({panelData, listData}){
    const {title, searchFunction, deleteFunction, editFunction, editContent, disableFunction} = panelData;
    const list = listData


    //#region borrar informacion
    const [deleteData, setDeleteData] = React.useState({open: false});
    const handleClickOpen = (id, titulo) => {
        var state = {
            id: id,
            titulo: titulo,
            open: true
        };
        setDeleteData(state);
    };
    if(deleteFunction){
        const handleClose = () => {
            setDeleteData({open: false});
        };
        const handleDelete = ()=>{
            deleteFunction(deleteData.id);
            handleClose();
        }
    
        var deleteDialog = (
            <Dialog
                open={deleteData.open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                >
                <DialogTitle id="alert-dialog-title">
                    {deleteData.titulo}
                </DialogTitle>
                <DialogActions>
                    <Button onClick={handleClose} autoFocus>Cancelar</Button>
                    <Button onClick={handleDelete}>Aceptar</Button>
                </DialogActions>
            </Dialog>
        );
    }
    //#endregion

    //#region editar informacion
    const [editData, setEditData] = React.useState({open: false});
    const handleEditClickOpen = (id, titulo) => {
        var state = {
            id: id,
            titulo: titulo,
            open: true
        };
        setEditData(state);
    };
    if(editFunction && editContent){
        const handleEditClose = () => {
            setEditData({open: false});
        };
        const handleEdit = ()=>{
            editFunction(editData.id, handleEditClose);
        }
        var editDialog = (
            <Dialog
                open={editData.open}
                onClose={handleEditClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                >
                <DialogTitle id="alert-dialog-title">
                    {editData.titulo}
                </DialogTitle>
                <DialogContent>
                    {editContent}
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleEditClose} autoFocus>Cancelar</Button>
                    <Button onClick={handleEdit}>Aceptar</Button>
                </DialogActions>
            </Dialog>
        );
    }
    //#endregion

    //#region filtro de busqueda
    //filto de busqueda de categorias
    const searchInputRef =  useRef();
    if(searchFunction){

        const handleSearch = ()=>{
            searchFunction(searchInputRef);
        }
        var searchContent =  (
            <Box sx={{ display: 'flex', alignItems: 'flex-end', marginTop: "20px" }}>
                <span className="material-icons">search</span>
                <TextField inputRef={searchInputRef} id="input-with-sx" label="Buscar" variant="standard" fullWidth={true} onChange={handleSearch}/>
            </Box>
        )
    }
    //#endregion


    //#region render
    const paperStyles = {
        bgcolor: '#3f903f',
        color: "#fff",
        fontSize: "18px",
        padding: 1,
        textAlign: "center"
    }

    return (
        <Fragment>
            <Paper sx={paperStyles} variant="outlined">
                {title}
            </Paper>
            {searchContent}
            <List>
                {list.map((item,index)=>{
                    return <ListContent key={index} item={item} 
                        hadleOpendelete={deleteFunction != null ? handleClickOpen: null}
                        handleOpenEdit={editFunction != null ? handleEditClickOpen: null }
                        handleDisableChange = {disableFunction != null ? disableFunction: null }
                        />}
                    )}
            </List>
            {deleteDialog}
            {editDialog}
        </Fragment>
    )
    //#endregion
}

function ListContent(props){
    const {item, hadleOpendelete, handleOpenEdit,handleDisableChange} = props;
    const secondary = item.secondaryText;

    const [activo, setSwitch] = useState(item.active)


    if(!item.visible)
        return;

    if(handleDisableChange){
        const handleChange = function(){
            var state = !activo
            setSwitch(state);
            handleDisableChange(item.id, state);
        }

        var disableSwitch = <FormControlLabel onChange={handleChange}
            control={<Switch edge="end" checked={activo} />}
            // label="activo"
            // labelPlacement="top"
        />
    }

    if(hadleOpendelete){
        const handleDeleteClick = ()=>{
            hadleOpendelete(item.id, "esta seguro que desea eliminar: " + item.name);
        }
        var deleteIcon =  
            <IconButton edge="end" aria-label="delete" onClick={handleDeleteClick}>
                <span className="material-icons">delete</span>
            </IconButton>
    }

    if(handleOpenEdit){
        const handleEditClick = ()=>{
            handleOpenEdit(item.id, "Editando: " + item.name);
        }
        var editIcon =  
            <IconButton edge="end" aria-label="edit" onClick={handleEditClick}>
                <span className="material-icons">edit</span>
            </IconButton>
    }

    return (
        <ListItem
            secondaryAction={
                <Fragment>
                    {editIcon}
                    {deleteIcon}
                    {disableSwitch}
                </Fragment>
            }
            >
            <ListItemAvatar>
                <Avatar>
                    <span className="material-icons">{item.icon}</span>
                </Avatar>
            </ListItemAvatar>
            <ListItemText
                primary={item.name}
                secondary={secondary}
            />
        </ListItem>
    )
}